@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
.btn-container{width:90vw;max-width:600px;margin:0 auto;margin-bottom:1.5rem;display:flex;justify-content:center;align-items:center}.btn-container .btn-desc{margin-bottom:0;font-size:1.2rem;font-weight:bold}.btn-container .btn-nav{margin:1rem;padding:0.25rem 0.5rem;text-transform:capitalize;font-weight:bold;border-color:transparent;background:#49a6e9;border-radius:5px;color:#fff;letter-spacing:1px;cursor:pointer}.btn-container .btn-nav:disabled{cursor:not-allowed}

.search{text-align:left;margin:5rem auto 3rem;width:90vw}.search__title{margin-bottom:1rem;letter-spacing:1px}.search__form-input{width:100%;max-width:600px;padding:0.5rem;text-transform:uppercase;letter-spacing:1px;border:0;border-bottom:2px solid #013d55;background-color:transparent;outline:none}

.stories{width:90vw;max-width:97rem;margin:0 auto;margin-bottom:5rem;display:grid;grid-gap:2rem;gap:2rem}@media screen and (min-width: 992px){.stories{display:grid;grid-template-columns:1fr 1fr}}.stories__story{background:#fff;border-radius:5px;padding:1rem 2rem;display:flex;flex-direction:column;justify-content:space-between;align-items:center}.stories__title{line-height:1.5;margin-bottom:0.25rem}.stories__info{margin-bottom:0.5rem;color:#617d98}.stories__read{font-size:0.85rem;margin-right:0.75rem;text-transform:capitalize;color:#49a6e9;letter-spacing:1px}.stories__remove{background:transparent;color:#bb2525;border-color:transparent;text-transform:capitalize;letter-spacing:1px;font-size:0.85rem}

body{background:#f1f5f8;font-size:62.5%;color:#102a42}*,*::before,*::after{box-sizing:border-box;margin:0;padding:0;font-family:"Poppins", sans-serif;font-size:16px}h2{font-size:2rem;line-height:2.6rem;text-transform:capitalize}h3{font-size:1.6rem;line-height:2rem}a{text-decoration:none;cursor:pointer}button{cursor:pointer}.container{width:100%;max-width:97rem;margin:0 auto;text-align:center}.spinner-container{position:relative}@-webkit-keyframes spinner{to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes spinner{to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.loading{width:6rem;height:6rem;margin:0 auto;margin-top:10rem;border-radius:50%;border:3px solid #ccc;border-top-color:#49a6e9;-webkit-animation:spinner 0.6s linear infinite;animation:spinner 0.6s linear infinite}

